body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: "Poppins";
  src: url("./fonts/Poppins-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.containertask {
  display: flex;
  max-width: 100vw;
  width: 100% !important;
  height: 111px;
  box-sizing: border-box;
  align-items: center;
  gap: 44.11%;
  background: #ffffff;
  box-shadow: 0px 5px 25px rgba(0, 0, 0, 0.1);
}
#todocontain {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding-left: 21.3%;
}
#Title2 {
  height: 53px;
  width: 170px;
  object-fit: contain;
}

#logoutdiv {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 30%;
}
#IconProfile {
  width: 42px;
  height: 42px;
  position: absolute;
  left: 75%;
}

#Logoutpng {
  width: 52px;
  height: 17px;
}

#ProfileDialogActions {
  display: flex;
  justify-content: flex-start;
  padding-left: 0;
}
#Dialogger {
  padding: 0;
}